html,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "SegoeUI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "HelveticaNeue", sans-serif;
  -webkit-tap-highlight-color: transparent;
  font-size: 1em;
  scroll-behavior: smooth;
}
html.loading,
body.loading {
  overflow: hidden;
}
html #root,
body #root {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  z-index: 0;
}
html #root nav,
body #root nav {
  position: fixed;
  top: 0;
  background: rgba(25, 25, 25, 0.5);
  width: 100%;
  padding: 0.5em 0;
  z-index: 1;
}
html #root nav.mobile,
body #root nav.mobile {
  position: sticky;
  padding-top: 0.5em;
  padding-bottom: 0;
}
html #root nav.mobile .navigation,
body #root nav.mobile .navigation {
  margin: auto 2em;
}
html #root nav.mobile ul,
body #root nav.mobile ul {
  margin: 0;
  padding: 0;
  flex-direction: column;
  border-top: 1px solid #999;
}
html #root nav.mobile ul li,
body #root nav.mobile ul li {
  border-bottom: 1px solid #999;
  padding: 1em;
}
html #root nav.mobile ul.shown,
body #root nav.mobile ul.shown {
  display: flex;
  flex-direction: column;
}
html #root nav.mobile .navigation,
body #root nav.mobile .navigation {
  justify-content: unset;
}
html #root nav ul,
body #root nav ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  column-gap: 1em;
}
html #root nav ul li a,
body #root nav ul li a {
  padding: 0 1em;
  color: #fff;
  text-decoration: none;
}
html #root nav ul li a:hover,
body #root nav ul li a:hover,
html #root nav ul li a:focus,
body #root nav ul li a:focus {
  color: #dc3545;
  text-decoration: underline;
}
html #root nav .navigation,
body #root nav .navigation {
  max-height: fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: auto;
}
html #root nav .navigation .icon img,
body #root nav .navigation .icon img {
  height: 3em;
  aspect-ratio: 1;
}
html #root nav .navigation .menu,
body #root nav .navigation .menu {
  outline: none;
  border: 0;
  background: transparent;
  padding: 0;
  color: #fff;
}
html #root nav .navigation .menu svg,
body #root nav .navigation .menu svg {
  font-size: 3em;
}
html #root nav .navigation .menu:hover,
body #root nav .navigation .menu:hover,
html #root nav .navigation .menu:focus,
body #root nav .navigation .menu:focus {
  cursor: pointer;
}
html #root nav .navigation .menu:hover svg,
body #root nav .navigation .menu:hover svg,
html #root nav .navigation .menu:focus svg,
body #root nav .navigation .menu:focus svg {
  color: #dc3545;
}
html #root .main-container,
body #root .main-container {
  position: static;
  top: 0;
  bottom: 0;
  z-index: 0;
}
html #root .main-container .container,
body #root .main-container .container {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
  align-items: left;
  justify-content: center;
}
html #root .main-container .container .slide,
body #root .main-container .container .slide {
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
}
html #root .main-container .container .title,
body #root .main-container .container .title {
  color: #fff;
  z-index: 0;
  font-size: 6em;
  margin: auto 1em;
  width: 100%;
  text-shadow: 4px 4px 6px #000;
  text-transform: uppercase;
}
html #root .main-container .container-1,
body #root .main-container .container-1 {
  background: url(/src/images/backgrounds/1.jpg) no-repeat;
  background-size: cover;
}
html #root .main-container .container-2,
body #root .main-container .container-2 {
  background: url(/src/images/backgrounds/2.jpg) no-repeat;
  background-size: cover;
}
html #root .main-container .container-3,
body #root .main-container .container-3 {
  background: url(/src/images/backgrounds/3.jpg) no-repeat;
  background-size: cover;
}
html #root .main-container .container-4,
body #root .main-container .container-4 {
  background: url(/src/images/backgrounds/4.jpg) no-repeat;
  background-size: cover;
}
html #root .main-container .container-5,
body #root .main-container .container-5 {
  background: url(/src/images/backgrounds/5.jpg) no-repeat;
  background-size: cover;
}
html #root .main-container .container-6,
body #root .main-container .container-6 {
  background: url(/src/images/backgrounds/6.jpg) no-repeat;
  background-size: cover;
}
@keyframes fade {
  0% {
    opacity: 1;
  }
  45% {
    opacity: 1;
  }
  55% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
html #root .main-container #home .slide.container-1,
body #root .main-container #home .slide.container-1 {
  animation: fade ease-in-out 10s infinite alternate;
}
